import { IParking } from '@interfaces';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Observable, Subscription } from 'rxjs';
import { RestaurantService } from '@services';
import { RestuarantParkingPayload } from '@payloads';
import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormGroup, FormControl, FormBuilder } from '@angular/forms';
import { Parking, ResponseDetail, Amenities } from '@models';
import { map, mergeMap } from 'rxjs/operators';

@Component({
  selector: 'app-parking',
  templateUrl: './parking.component.html',
  styleUrls: ['./parking.component.scss']
})
export class ParkingComponent implements OnInit, OnDestroy {
  parkingForm: FormGroup = this.fb.group({
    parking: new FormControl([])
  });
  parkings: Array<Parking> = [];
  currentParkings: Array<number>;
  restaurantId: number = parseInt(localStorage.getItem('restaurantId'), 10);
  restaurantSubscription = Subscription.EMPTY;
  parkingSubscription = Subscription.EMPTY;

  constructor(
    private fb: FormBuilder,
    private restaurantService: RestaurantService,
    private snackBar: MatSnackBar
  ) {}

  ngOnInit(): void {
    this.restaurantSubscription = this.getRestaurantDetails(this.restaurantId)
      .pipe(
        mergeMap((response: ResponseDetail): any => {
          this.currentParkings = response.restaurant.parking
            .filter((pParking: IParking) => pParking.active === true)
            .map((pParking: IParking) => pParking.id);

          this.parkingForm.patchValue({ parking: this.currentParkings });

          return this.getParkingOptions();
        })
      )
      .subscribe((response: any) => {
        this.createParkingObjects(response as Array<Parking>);
      });
  }

  ngOnDestroy() {
    this.restaurantSubscription.unsubscribe();
    this.parkingSubscription.unsubscribe();
  }

  onParkingCheck(parking: IParking): void {
    parking.active = !parking.active;

    let currentSelectedParking = this.parkingForm.get('parking').value;

    currentSelectedParking = this.onParkingSelection(
      parking,
      currentSelectedParking
    );

    this.parkingForm.patchValue({
      parking: currentSelectedParking
    });
  }

  onSubmit(): void {
    const parkingPayload = new RestuarantParkingPayload(this.parkingForm);

    this.parkingSubscription = this.restaurantService
      .setRestaurantAmenities(parkingPayload.toJson(), this.restaurantId)
      .subscribe(() => this.openSnackBar('Cambios guardados', 'Entendido'));
  }

  private createParkingObjects(parkings: Array<Parking>): void {
    const parkingOptions = parkings;

    parkingOptions.forEach(pParking => {
      const parkingObject = {
        description: pParking.description,
        id: pParking.id,
        active: false
      };

      const isActive = this.currentParkings.find(id => id === pParking.id);

      if (isActive) {
        parkingObject.active = true;
      }

      this.parkings.push(parkingObject);
    });
  }

  private getParkingOptions(): Observable<Array<Parking>> {
    return this.restaurantService.getAmenities().pipe(
      map((response: Amenities) => {
        const parkings: Array<Parking> = response.parkings.map(
          (pParking: IParking) => {
            return new Parking(pParking);
          }
        );

        return parkings;
      })
    );
  }

  private getRestaurantDetails(id: number): Observable<ResponseDetail> {
    return this.restaurantService.getRestaurantDetail(id);
  }

  private onParkingSelection(
    parking: IParking,
    currentParkings: Array<number>
  ): Array<number> {
    const isParkingSelected = currentParkings.includes(parking.id);

    if (isParkingSelected) {
      const index = currentParkings.indexOf(parking.id);

      currentParkings.splice(index, 1);
    } else {
      currentParkings.push(parking.id);
    }

    return currentParkings;
  }

  private openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 2000
    });
  }
}
